<template>
  <v-container>

      <v-slider
          hint="BPM"
          v-model="bpm"
          max="240"
          min="60"
          :label="'BPM:'+bpm"
      ></v-slider>
    <v-row>
      <v-col cols="2" align-self="center" align="right">
        <v-btn @click="togglePlay"><v-icon v-text="playing?'mdi-pause':'mdi-play'"></v-icon></v-btn></v-col>
      <v-col cols="10" align-self="center" align="left">
        <table style="margin: auto">
          <tr>
            <th></th>
            <th v-for="step in beats.length" :key="step">
              <div class="beatCol" :class="{'beatCol__on':currentBeat==step}"></div>
            </th>
          </tr>
          <tr v-for="track in tracks" :key="track">
            <td align="right">{{trackNames[track-1]}}</td>
            <td v-for="step in beats.length" :key="step">
              <div class="beatCol" :class="{'beatCol__on':beats[step-1].notes[track-1].value}" @click="toggleNote(step-1, track-1)">
              </div>
            </td>
          </tr>
          <tr>
            <td align="right">Chord</td>
            <td v-for="step in beats.length" :key="step" style="vertical-align: center">
              <v-menu
                  top :close-on-content-click="false"
              >
                <template v-slot:activator="{ on, attrs }">
                  <div class="chordCol"  v-bind="attrs"
                       :class="{'beatCol__on':beats[step-1].chord.note}"
                       v-on="on">{{beats[step-1].chord.note}}{{beats[step-1].chord.chord}}
                  </div>
                </template>
                <v-list>
                  <v-list-item>
                    <v-select v-model="beats[step-1].chord.note" :items="['C','C#','D','D#','E','F','F#','G','G#','A','A#','B']" label="Note"></v-select>
                  </v-list-item>
                  <v-list-item>
                    <v-select v-model="beats[step-1].chord.chord" :items="['M','m','7','m7','M7','dim','dim7','sus2','sus4','aug']" label="Chord"></v-select>
                  </v-list-item>
                  <v-list-item>
                    <v-btn @click="beats[step-1].chord={}">Delete</v-btn>
                  </v-list-item>
                </v-list>
              </v-menu>
            </td>
          </tr>
        </table>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
const { ChordType, Chord, Note } = require("@tonaljs/tonal");

let Tone;
export default {
  name: "Beats",
  data: function () {
    return {
      bpm: 120,
      playing:false,
      tracks:3,
      trackNames:['Kick','Snare','Hat'],
      currentBeat:0,
      beats:[
        {beat:1, notes:[{value:false},{value:false},{value:false}],chord:{note:'D',chord:'M'}},
        {beat:2, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:3, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:4, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:5, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:6, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:7, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:8, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:9, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:10, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:11, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:12, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:13, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:14, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:15, notes:[{value:false},{value:false},{value:false}],chord:{}},
        {beat:16, notes:[{value:false},{value:false},{value:false}],chord:{}},
      ],
      sequencer:null

    }
  },
  watch:{
    bpm:function(val){
      if(this.synths){
        Tone.Transport.bpm.value = val;
      }
    }
  },
  computed:{
    chordTypes(){
      return ChordType.all()
          .map(get => get.name);
    }
  },
  methods: {
    async initSynths(){
      if (!Tone) {
        Tone = await import('tone');
      }
      if (!this.synths) {
        this.polySynth = new Tone.PolySynth().toDestination();
        this.synths = await new Promise(resolve => {
              let sampler = new Tone.Sampler({
                    urls: {
                      A1: "kick.mp3",
                      A2: "snare.mp3",
                      A3: "hihat.mp3",
                    },
                    baseUrl: "./",
                    onload: () => {
                      resolve(sampler);
                    }
                  }
              ).toDestination()
            }
        )
      }
      Tone.Transport.bpm.value = this.bpm;
    },
    async toggleNote(x, y){
      console.log(x, y);
      this.beats[x].notes[y].value=!this.beats[x].notes[y].value;
      await this.updateSequencer();
    },
    async updateSequencer(){
      await this.initSynths();
      if (Tone.context.state !== 'running') {
        Tone.context.resume();
      }
      if (!this.sequencer){
        // this.sequencer.dispose();
        this.sequencer = new Tone.Sequence((time, event) => this.handleTransport(time, event), this.beats).start(0);
      }
    },
    async togglePlay() {
      await this.updateSequencer();
      if(!this.playing){
        Tone.Transport.start();
      }else{
        Tone.Transport.pause();
      }
      this.playing=!this.playing;
    },
    handleTransport(time,event){
      if(event.chord.note && event.chord.chord){
        this.polySynth.releaseAll();
        let chordNotes = Chord.getChord(event.chord.chord, event.chord.note+'4').notes;
        chordNotes.forEach(function(note, index) {
          chordNotes[index] = Note.simplify(note);
        });
        this.polySynth.triggerAttackRelease(chordNotes, '8n');
      }
      if(event.notes[0].value){
        this.synths.triggerAttackRelease("A1", "16n", time);
      }
      if(event.notes[1].value){
        this.synths.triggerAttackRelease("A2", "16n", time);
      }
      if(event.notes[2].value){
        this.synths.triggerAttackRelease("A3", "16n", time);
      }

      // Draw.schedule takes a callback and a time to invoke the callback
      Tone.Draw.schedule(() => {
        // the callback synced to the animation frame at the given time
        this.currentBeat=event.beat;
        // setTimeout(() => {
        //   noteElement.classList.remove("active");
        // }, 100);
      }, time);
    }
  },
  created() {

  }
}
</script>
<style>
td>div{
  height: 40px;
  width: 40px;
  margin: 0;
  padding: 0;
  background-color: dimgray;
}
th>div{
  height: 40px;
  width: 40px;
  margin: 0;
  padding: 0;
  background-color: whitesmoke;
}
th>.beatCol__on{
  background: darkred;
}
.beatCol__on{
  background: aqua;
}
.chordCol{
  font-size: small;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>